@import "../variables.less";

.page {
  font-family: "PT Sans", "Calibri", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  font-variant: normal;
  font-size: @font-size-regular;
  line-height: @line-height-regular;
  background-color: @bg-color-white;
  min-height: 100%;

  &__body {
    position: relative;
    text-align: center;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: normal;
    margin: 0 auto;
    padding: 0;
    display: flex;
    flex-direction: column;
    min-width: 320px;
    height: 100%;
    align-self: stretch;
  }
}

