@import "../variables.less";

.hero {
  padding-top: 55px;
  overflow: hidden;

  &__image {
    display: block;
    margin: 0 auto;
    box-sizing: border-box;
    width: 100%;
    height: 389px;
    background-color: @bg-color-blue;
    background-image:
      url("../../img/shapes/hero-triangle.svg"),
      url("../../img/background/main-header-320x389.jpg");
    background-size: contain, cover;
    background-position: 0% 100%, 0 0;
    background-repeat: no-repeat;
    padding-left: 18px;
    padding-right: 18px;
    padding-bottom: 85px;
    padding-top: 90px;
  }

  &__slogan {
    .common-box();
    margin-top: 39px;
    overflow-wrap: break-word;
  }
}
