@import "../variables.less";

.main-nav-item-height() {
  min-height: 55px;
}

.main-nav {
  position: relative;
  background-color: @bg-color-blue;

  &__list {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    border: 0;
    padding: 0;
    margin: 0;
    list-style-type: none;
  }

  &__item {
    .main-nav-item-height();
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }

  // &__item--active{

  // }

  &__link {
    display: block;
    width: 100%;
    padding-top: 13px;
    padding-bottom: 13px;
    text-decoration: none;
    color: @bg-color-white;
  }

  &__button {
    .main-nav-item-height();
    width: 23px;
    display: block;
    position: absolute;
    right: 18px;
    background-repeat: no-repeat;
    background-color: transparent;
    background-position: center center;
    background-size: contain;
    cursor: pointer;
  }

  &__button--close {
    background-image: url("../../img/nav-button/menu-close.svg");
  }

  &__button--close:hover {
    background-image: url("../../img/nav-button/menu-close-hover.svg");
  }

  &__button--close:active {
    background-image: url("../../img/nav-button/menu-close-down.svg");
  }

  &__button--open {
    background-image: url("../../img/nav-button/menu-open.svg");
  }

  &__button--open:hover {
    background-image: url("../../img/nav-button/menu-open-hover.svg");
  }

  &__button--open:active {
    background-image: url("../../img/nav-button/menu-open-down.svg");
  }
}
