@font-face {
  font-family: "PT Sans";
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  font-variant: normal;
  src: local("PT Sans"),
    url("../fonts/ptsans.woff2") format("woff2"),
    url("../fonts/ptsans.woff") format("woff"),
    url("https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&display=swap");
}

@font-face {
  font-family: "PT Sans";
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  font-variant: normal;
  src: local("PT Sans"),
    url("../fonts/ptsansbold.woff2") format("woff2"),
    url("../fonts/ptsansbold.woff") format("woff");
}
