@import "./variables.less";

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.heading {
  font-weight: 700;
  font-size: @font-size-heading;
  line-height: @line-height-heading;
}

.common-box() {
  margin: 0 auto;
  padding: 0 18px;
}
